<template>
  <div>
    <article v-for="(value, key, index) in tshirts.tshirtList">
      <figure>
        <p><img :src="getUrlImg(value)" :alt="getTshirtName(value)"></p>
        <figcaption>
          <p>{{getTshirtName(value)}}</p>
          <button class="button green" type="button">add to cart</button>
        </figcaption>
      </figure>
    </article>
    <button type="button"
            class="button blue"
            :class="{
              'gray': tshirtsLoaded,
              'blue': !tshirtsLoaded,
            }"
            @click="nextPage()">add to cart
    </button>
  </div>
</template>

<script>
import languagePack  from './lang/languages.js'

export default {
  name: 't-shirts',
  props: {
    tshirts: {
      required: true,
      type: Object,
    },
    tshirtsLoaded: {
      required: true,
      type: Boolean,
    },
  },
  methods: {
    // /uploads/photo/tshirts/[model]/[brands_name]/[id]
    getUrlImg(TshirtObj) {
      return this.tshirts.config.preview.path + '/'
        + TshirtObj.model + '/'
        + TshirtObj.brand_translit + '/'
        + TshirtObj.id + '/'
        + JSON.parse(TshirtObj.images)[0];
    },
    getTshirtName(TshirtObj) {
      return TshirtObj['name_' + this.language]
    },
    nextPage() {
      this.$emit("nextPage", this.tshirts.paginate.current_page + 1);
    },
  },
  messages: languagePack,
}
</script>

<style lang="scss" scoped>
  figure img {
    width: 230px;
  }
</style>



